*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    
}

html, body
{
    overflow-x: hidden;
    font-family: 'Poppins';
    
}
.webgl
{   
    position: relative;
    z-index: -1;
    width: 100%;
    height: 100vh;
    background: #010101;
    background-blend-mode: color;
    
}

